import {stringify} from "comment-json";
import {ElementsTree} from "../../../../model/ElementsTree";
import {Graph} from "../../../../model/Graph";
import {Playshot} from "../../../../model/Playshot";
import {getNodeIdFromProophBoardDescription} from "../../descriptions";
import {PlayshotChanged} from "../PlayshotChanged";
import {PlayInformationRegistry} from "./information-definitions";
import {InformationAdded} from "./InformationAdded";
import {prepareDiffInformation} from "./prepare-diff-information";

export const compareInformation = (oldPlayshot: Playshot, newPlayshot: Playshot, graph: Graph, elementsTree: ElementsTree): PlayshotChanged[] => {
  const oldTypes: PlayInformationRegistry = oldPlayshot.playConfig.types;
  const newTypes: PlayInformationRegistry = newPlayshot.playConfig.types;

  const changeSet: PlayshotChanged[] = [];

  const oldNames = Object.keys(oldTypes);
  const newNames = Object.keys(newTypes);

  const informationToRemove: string[] = oldNames.filter(oldName => !newNames.includes(oldName));
  const informationToAdd: string[] = newNames.filter(newName => !oldNames.includes(newName));

  const changedInformation: string[] = newNames.filter(newName => {
    if(informationToAdd.includes(newName)) {
      return false;
    }

    const oldInformation = oldTypes[newName];
    const newInformation = newTypes[newName];

    const oldPreparedInformation = prepareDiffInformation(oldInformation, oldPlayshot);
    const newPreparedInformation = prepareDiffInformation(newInformation, newPlayshot);

    return stringify(oldPreparedInformation) !== stringify(newPreparedInformation);
  })

  informationToAdd.forEach(infoNameToAdd => {
    const infoToAdd = newTypes[infoNameToAdd];

    // @TODO: check if info was renamed by looking up NodeId in elementsTree

    changeSet.push(new InformationAdded(infoNameToAdd, getNodeIdFromProophBoardDescription(infoToAdd.desc), infoToAdd, newPlayshot));
  })

  return changeSet;
}
