import {stringify} from "comment-json";
import {ElementsTree} from "../../../../model/ElementsTree";
import {Graph} from "../../../../model/Graph";
import {Playshot} from "../../../../model/Playshot";
import {getNodeIdFromProophBoardDescription} from "../../descriptions";
import {PlayshotChanged} from "../PlayshotChanged";
import {PlayCommandRegistry} from "./command-definitions";
import {CommandAdded} from "./CommandAdded";

export const compareCommands = (oldPlayshot: Playshot, newPlayshot: Playshot, graph: Graph, elementsTree: ElementsTree): PlayshotChanged[] => {
  const oldCommands: PlayCommandRegistry = oldPlayshot.playConfig.commands;
  const newCommands: PlayCommandRegistry = newPlayshot.playConfig.commands;

  const changeSet: PlayshotChanged[] = [];

  const oldNames = Object.keys(oldCommands);
  const newNames = Object.keys(newCommands);

  const commandsToRemove: string[] = oldNames.filter(oldName => !newNames.includes(oldName));
  const commandsToAdd: string[] = newNames.filter(newName => !oldNames.includes(newName));

  const changedCommands: string[] = newNames.filter(newName => {
    if(commandsToAdd.includes(newName)) {
      return false;
    }

    const oldCommand = oldCommands[newName];
    const newCommand = newCommands[newName];
    const oldRules = oldPlayshot.playConfig.commandHandlers[newName] || [];
    const newRules = newPlayshot.playConfig.commandHandlers[newName] || [];


    return stringify(oldCommand) !== stringify(newCommand) || stringify(oldRules) !== stringify(newRules);
  })

  commandsToAdd.forEach(cmdNameToAdd => {
    const cmdToAdd = newCommands[cmdNameToAdd];

    // @TODO: check if command was renamed by looking up NodeId in elementsTree

    changeSet.push(new CommandAdded(cmdNameToAdd, getNodeIdFromProophBoardDescription(cmdToAdd.desc), cmdToAdd, newPlayshot));
  })

  return changeSet;
}
