import {stringify} from "comment-json";
import {ElementsTree} from "../../../../model/ElementsTree";
import {Graph} from "../../../../model/Graph";
import {Playshot} from "../../../../model/Playshot";
import {getNodeIdFromProophBoardDescription} from "../../descriptions";
import {PlayshotChanged} from "../PlayshotChanged";
import {PlayEventRegistry} from "./event-definitions";
import {EventAdded} from "./EventAdded";

export const compareEvents = (oldPlayshot: Playshot, newPlayshot: Playshot, graph: Graph, elementsTree: ElementsTree): PlayshotChanged[] => {
  const oldEvents: PlayEventRegistry = oldPlayshot.playConfig.events;
  const newEvents: PlayEventRegistry = newPlayshot.playConfig.events;

  const changeSet: PlayshotChanged[] = [];

  const oldNames = Object.keys(oldEvents);
  const newNames = Object.keys(newEvents);

  const eventsToRemove: string[] = oldNames.filter(oldName => !newNames.includes(oldName));
  const eventsToAdd: string[] = newNames.filter(newName => !oldNames.includes(newName));

  const changedEvents: string[] = newNames.filter(newName => {
    if(eventsToAdd.includes(newName)) {
      return false;
    }

    const oldEvent = oldEvents[newName];
    const newEvent = newEvents[newName];

    return stringify(oldEvent) !== stringify(newEvent);
  })

  eventsToAdd.forEach(evtNameToAdd => {
    const evtToAdd = newEvents[evtNameToAdd];

    // @TODO: check if event was renamed by looking up NodeId in elementsTree

    changeSet.push(new EventAdded(evtNameToAdd, getNodeIdFromProophBoardDescription(evtToAdd.desc), evtToAdd, newPlayshot));
  })

  return changeSet;
}
